import { Box, Stack } from "@mui/material";
import { Chat_History } from "../../data";
import {
  TextMsg,
  Timeline,
  MediaMsg,
  ReplyMsg,
  LinkMsg,
  DocMsg,
} from "./MegTypes";
import React from "react";

const Message = () => {
  return (
    <Box p={3}>
      <Stack spacing={3}>
        {Chat_History.map((el) => {
          switch (el.type) {
            case "divider":
              return <Timeline el={el} />;
            case "msg":
              switch (el.subtype) {
                case "img":
                  //Image msg
                  return <MediaMsg el={el} />;
                case "doc":
                  //Doc msg
                  return <DocMsg el={el} />;
                case "link":
                  //link msg
                  return <LinkMsg el={el} />;
                case "reply":
                  //reply msg
                  return <ReplyMsg el={el} />;
                default:
                  // text msg
                  return <TextMsg el={el} />;
              }
              break;

            default:
              return <></>;
          }
        })}
      </Stack>
    </Box>
  );
};

export default Message;
